table thead tr th {
    font-weight: 700;
    font-size: 14px;
    line-height: 17px;
    padding: 0.75rem 1rem;
    position: sticky
}

table tbody tr td {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    padding: 1rem 1rem;
}

/* Custom Styles for Scrollbar */
.table-container {
    border-collapse: collapse;
    width: 100%;
}

.table-container::-webkit-scrollbar {
    width: 12px;
    height: 17px;
}

.table-container::-webkit-scrollbar-track {
    background: #D1D1DB;
}

.table-container::-webkit-scrollbar-thumb {
    background-color: #383751;
}
