@import url('https://fonts.googleapis.com/css2?family=Red+Hat+Display:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@tailwind base;
@tailwind components;
@tailwind utilities;

* {
  font-family: 'Red Hat Display', sans-serif;
}

.parallelogram-button {
  transform: skew(-20deg);
}

.seller-table {
  /* webkit fill available */
  max-height: -webkit-fill-available;
}

/* Add these media queries to your existing CSS file or create a new one */
@media screen and (max-width: 768px) {
  .featured-products-slider .slick-list {
    overflow: hidden; /* Prevents the slider from overflowing its container */
  }
}

@media screen and (max-width: 480px) {
  .featured-products-slider .slick-track {
    display: flex;
  }
}


/* custom scrollbar for select-category-div */

.select-category-div::-webkit-scrollbar {
  width: 8px;
}

.select-category-div::-webkit-scrollbar-thumb {
  background-color: #a9a7a7;
  border-radius: 10px;
}

.select-category-div::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}
